import React from "react";
import { connect } from "react-redux";
import echarts from "echarts";

import "../../node_modules/react-vis/dist/style.css";
import  "../../node_modules/bootstrap/dist/css/bootstrap.min.css";
import   "../../node_modules/bootstrap/dist/js/bootstrap.min.js";
// a histogram to show the number of cars left vs the number of questions asked.


class Histogram extends React.Component {
  constructor(props) {
    super(props);
    this.state = {
      hintValue: null
    };
  }

  _onMouseLeave = () => {
    this.setState({
      hintValue: null
    });
  };

  _onNearestX = (value, { index }) => {
    this.setState({
      hintValue: this.props.data[index]
    });
  };
  componentDidMount(){
    this.init(this.props.data)
  }
  componentWillUpdate(nextProps,nextState){
    this.init(nextProps.data)
  }
  init=(echartsData)=>{
    let  myChart = echarts.init(this.mapChart);
    let  option = null;
    let xAxisData = [];
    let seriesData = [];
    echartsData.map((v,i)=>{
      xAxisData.push(`no of question${i}`)
      seriesData.push(v.y)
    })
     option = {
      tooltip: {
          trigger: 'axis',
          axisPointer: {
              type: 'none'
          },
          formatter: function (params) {
              return params[0].name + ': ' + params[0].value;
          }
      },
      xAxis: {
          data: xAxisData,
          axisTick: {show: true},
          axisLine: {show: true},
          axisLabel: {
              color: '#e54035'
          }
      },
      yAxis: {
          splitLine: {show: true},
          axisTick: {show: true},
          axisLine: {show: true},
          axisLabel: {show: true}
      },
      color: ['#e54035'],
      series: [{
          name: 'hill',
          type: 'pictorialBar',
          barCategoryGap: '-130%',
          symbol: 'path://M0,10 L10,10 L5,0 L0,10 z',
          symbol: 'path://M0,10 L10,10 C5.5,10 5.5,5 5,0 C4.5,5 4.5,10 0,10 z',
          itemStyle: {
              opacity: 0.5
          },
          emphasis: {
              itemStyle: {
                  opacity: 1
              }
          },
          data: seriesData,
          z: 10
      },]
  };
  if (option && typeof option === "object") {
      myChart.setOption(option, true);
  }
    }
  render() {
    return (
      <div class="col-md-5 column">
        <h2>No. of Players Left</h2>
        <div style={{width:"30rem", height: "30rem",textAlign:"center",marginLeft:"5rem"}}ref={(e)=>{this.mapChart = e}}>            
        </div>
        </div>
    );
  }
}

const mapStateToProps = ({ numLeftPoints }) => ({
  data: numLeftPoints.map((num, i) => ({ x: i, y: num }))
});

export default connect(mapStateToProps)(Histogram);
