import React from "react";
import { connect } from "react-redux";


import Highcharts from "highcharts/highcharts.js";
import "../../node_modules/react-vis/dist/style.css";
import  "../../node_modules/bootstrap/dist/css/bootstrap.min.css";
import   "../../node_modules/bootstrap/dist/js/bootstrap.min.js";

// a histogram to show the number of cars left vs the number of questions asked.

class Comparison extends React.Component {
  constructor(props) {
    super(props);
    this.state = {
      hintValue: null
    };
    this.chart =[];
    this.noSortList=[];
    this.sortList =[];
  }

/**
 * 
 * 
 *  rendervaule = {this.props.numLeftPoints_noSort}
    rendervauleSort = {this.props.numLeftPoints}
 */


  _onMouseLeave = () => {
    this.setState({
      hintValue: null
    });
  };

  componentDidUpdate(){
    this.noSortList.push(this.props.numLeftPoints_noSort);
    alert(this.noSortList);
    this.chart =new Highcharts.chart('container2',{
      chart: {
        type: 'column'
      },
      colors:['#BA55D3','#7CB5EC'],
      title:{
        text:'<b>Sorting-Simplex VS Simplex</b>'
      },
      credits:{
        enabled:false
      },
      xAxis: {
        categories: [
          'first round','second round','third round','forth round'],
        crosshair: true
      },
      yAxis: {
        min: 0,
        title: {
          text: '<b>Number of remaining player<b>'
        }
      },
      tooltip: {
        // head + 每个 point + footer 拼接成完整的 table
        headerFormat: '<span style="font-size:10px">{point.key}</span><table>',
        pointFormat: '<tr><td style="color:{series.color};padding:0">{series.name}: </td>'+
        '<td style="padding 0"><b>{point.y}</b></td></tr>',
        footerFormat: '</table>',
        shared: true,
        useHTML: true
      },
      plotOptions: {
        column: {
          borderWidth: 0
        }
      },
      series: [ {
        name: 'Simplex',
        data: this.noSortList
      },
      {
        name: 'Sorting-Simplex',
        data: this.props.numLeftPoints
      }]
    });
  }

  render() {
    return (
      <div class="col-md-5 column">
        
        <div 
        id="container2"
        style={{marginLeft:"9rem"}}
        >                  
        </div>
        </div>
        );
  }
}


const mapStateToProps = ( {
  numLeftPoints_noSort,
  numLeftPoints
}) => 
({
  numLeftPoints_noSort,
  numLeftPoints
}
 
);

export default connect(mapStateToProps)(Comparison);