import React from "react";
import { connect } from "react-redux";
import { setActiveComponent, restart } from "../actions";
import classNames from "classnames";


// show the number of questions asked and the favourite car/remaining cars.
function Result({
  labels,
  candidates,
  leftPoints,
  attributes,
  mask,
  numLeftPoints,
  restart
}) {
  let ths = [<th style={{textAlign:"center"}} key="Name">Name</th>];
  attributes.forEach(([attr, config]) => {
    if (mask[attr]) {
      const th = <th style={{textAlign:"center"}} key={attr}>{attr}</th>;
      ths.push(th);
    }
  });
  const trs = leftPoints.map((idx, i) => (
    <tr key={i} data-toggle="tooltip" title={labels[idx]}>
      <td  key={labels[idx]}>{labels[idx]}</td>
       {candidates[idx].map((x, j) => (
        <td key={j}>{x}</td>
      ))}
    </tr>
  ));
  return (
    <div className="justify-content-center">
      <h4>The Total No. of Questions Asked is: {numLeftPoints.length - 1}.</h4>
      <h4>
        {leftPoints.length === 1
          ? "Your Favourite Player is:"
          : `${leftPoints.length} Players Left in the Database:`}
      </h4>
      <table
        className={classNames("table", "table-hover", {
          "table-fixed": trs.length > 7
        })}
        style={{
          maxWidth: "40rem",
          margin: "auto"
        }}
      >
        <thead>
          <tr>{ths}</tr>
        </thead>
        <tbody>{trs}</tbody>
      </table>
      <div>
        <button type="button" className="btn btn-primary" onClick={restart}>
          Return to Welcome
        </button>
      </div>
     
    </div>
  );
}

const mapStateToProps = ({
  labels,
  candidates,
  leftPoints,
  attributes,
  mask,
  numLeftPoints,
  numLeftPoints_noSort,
}) => ({
  labels,
  candidates,
  leftPoints,
  attributes,
  mask,
  numLeftPoints,
  numLeftPoints_noSort
});

const mapDispatchToProps = dispatch => ({
  restart: () => {
    dispatch(setActiveComponent("Welcome"));
    dispatch(restart());
  }
});

export default connect(
  mapStateToProps,
  mapDispatchToProps
)(Result);
