import React from "react";
import { connect } from "react-redux";
import {
  setActiveComponent,
  setLeftPoints,
  setLeftPoints_noSort,
  prunePoints,
  updateConvexHull,
} from "../actions";
import {
  array2Vector2D,
  vector2Array,
  getPrunedIndices,
  vector2Array2D
} from "../utils";

import  "bootstrap/js/popover.js";
import  "bootstrap/dist/css/bootstrap.min.css";
import   "bootstrap/dist/js/bootstrap.min.js";

import {Button} from "antd";
import "antd/dist/antd.css";

const SIMPLEX = 2;
const RANDOM = 1;
var choose_perround = 0;
var choose_arrs =[0,0,0];
var  dragelement=0;
var  putelement=0;
var  sort_list_index =[0,1,2];
var  questionsnum = 3;
// iteratively ask the user to choose the preferred car from a pair of cars.
class Question extends React.Component {
  constructor(props) {
    super(props);
    const smallerBetter = new window.Module.VectorInt();
    this.attributes = [];
    this.props.attributes.forEach(([attr, config]) => {
      if (this.props.mask[attr]) {
        smallerBetter.push_back(config.smallerBetter ? 1 : 0);
        this.attributes.push(attr);
      }
    });
    const candidatesVec = array2Vector2D(this.props.candidates);
    this.runner = new window.Module.AlgorithmRunner(
      candidatesVec,
      smallerBetter,
      this.props.mode === "random" ? RANDOM : SIMPLEX
    );
    candidatesVec.delete();
    this.prevIndices = this.runner.getCandidatesIndices();
    this.props.setLeftPoints(vector2Array(this.prevIndices));
    const num_noSort = this.runner.numLeftPoints_noSort();
    this.props.setLeftPoints_noSort(num_noSort);
    if (this.prevIndices.size() <= 2) {
      this.state = { pair: [] };
      this.stopInteraction();
    } else {
      const vec = window.Module.readConvexHullVertices();
      const convexHullVertices = vector2Array2D(vec);
      vec.delete();
      this.props.updateConvexHull(convexHullVertices);
      const indices = this.runner.nextPair(questionsnum);
      this.state = {
        pair: vector2Array(indices)
      };
    }

  }
  /**-------the section of functions(begin)-------- */
    // 拖拽开始
dragStart=(e,item)=>{
  //e.dataTransfer.setData('item',JSON.stringify(item));// 拖拽元素携带的数据
  if(dragelement === 0)dragelement = e.currentTarget;
  //alert(e.currentTarget.innerHTML);
};

// 拖拽元素经过放置元素时
dragOver = (e)=> {
  e.preventDefault();// 此处的代码是必须的  不然无法拖拽
  //alert(e.currentTarget.innerHTML);
};

// 拖拽元素放到放置元素时
drop=(e)=>{
  putelement = e;
  e.preventDefault();
  // 放置之后的后续操作
  let length = dragelement.childElementCount;
  let  zhongjianvalue;
  let  variable = sort_list_index[parseInt(dragelement.childNodes[0].innerText)-1];
  sort_list_index[parseInt(dragelement.childNodes[0].innerText)-1]  = sort_list_index[parseInt(putelement.currentTarget.childNodes[0].innerText)-1];
  sort_list_index[parseInt(putelement.currentTarget.childNodes[0].innerText)-1]  = variable;
  for(let i=1;i<length;i++){
    zhongjianvalue = dragelement.childNodes[i].innerText;
    dragelement.childNodes[i].innerText = putelement.currentTarget.childNodes[i].innerText;
    putelement.currentTarget.childNodes[i].innerText  = zhongjianvalue ;
  }
  putelement = dragelement = 0;
  
};


//ascending  by attributes;
ascSortfunction=(e)=>{
  let strname = e.currentTarget.parentElement.parentElement.innerText;
  let  sortindex = 0;
  //
  if(strname === "Points") sortindex=1;
  if(strname === "Rebounds") sortindex=2;
  if(strname === "Steals") sortindex=3;
  
  let  tablevalue =this.refs.items;
  let  tbodyvalue = tablevalue.childNodes[1];//tbodyvalue


   const datalist = [];
   const  namelist =[];
   let each_data_len = 0;  each_data_len = tbodyvalue.firstChild.childElementCount-2;
   let  data_count = 0; data_count = tbodyvalue.childElementCount;
   let sortlist =new Array(data_count);
   let flag_sortlist =new Array(data_count);
   for(let i = 0; i < data_count;i++){
    sortlist[i] = flag_sortlist[i] = 0;
   }
   this.state.pair.map((idx) => {
     namelist.push(this.props.labels[idx]);
    this.props.candidates[idx].forEach((x) => {
      datalist.push(parseInt(x));
    });
  });
  let   max_record = 0;
  for(let k = 0;k< data_count;k++){
//find  k-th  max data;
 for(let i = 0;i<data_count;i++){
    if(!flag_sortlist[i]) {max_record = i;break;}
 }    
 for(let j = 0;j < data_count;j++){
    if(datalist[max_record * each_data_len + sortindex -1] >= datalist[j * each_data_len + sortindex -1] && max_record !== j && flag_sortlist[max_record] === 0 && flag_sortlist[j] === 0){
       max_record =j;
     } 
   }
 flag_sortlist[max_record] =1;
 sortlist[k] = max_record;
}

  // sort the trs
 for(let i = 0 ;i < data_count;i++){
     let  cur_tr = tbodyvalue.childNodes[i];
     for(let j = 0;j < each_data_len;j++){
       cur_tr.childNodes[j+2].innerText = datalist[sortlist[i]*each_data_len+j]//row: sortlist[i]  col:j
     }
     cur_tr.childNodes[1].innerText = namelist[sortlist[i]];
   }
sort_list_index = sortlist;

};

//descending  by attributes;
dscSortfunction=(e)=>{
  let strname = e.currentTarget.parentElement.parentElement.innerText;
  let  sortindex = 0;
  //
  if(strname === "Points") sortindex=1;
  if(strname === "Rebounds") sortindex=2;
  if(strname === "Steals") sortindex=3;
  
  let  tablevalue =this.refs.items;
  let  tbodyvalue = tablevalue.childNodes[1];//tbodyvalue


   const datalist = [];
   const namelist = [];
   let each_data_len = 0;  each_data_len = tbodyvalue.firstChild.childElementCount-2;
   let  data_count = 0; data_count = tbodyvalue.childElementCount;
   let sortlist =new Array(data_count);
   let flag_sortlist =new Array(data_count);
   for(let i = 0; i < data_count;i++){
    sortlist[i] = flag_sortlist[i] = 0;
   }
   this.state.pair.map((idx) => {
    namelist.push(this.props.labels[idx]);
    this.props.candidates[idx].forEach((x) => {
      datalist.push(parseInt(x));
    });
  });
  let   max_record = 0;
  for(let k = 0;k< data_count;k++){
//find  k-th  max data;
 for(let i = 0;i<data_count;i++){
    if(!flag_sortlist[i]) {max_record = i;break;}
 }    
 for(let j = 0;j < data_count;j++){
    if(datalist[max_record * each_data_len + sortindex -1] >= datalist[j * each_data_len + sortindex -1] && max_record !== j && flag_sortlist[max_record] === 0 && flag_sortlist[j] === 0){
       max_record =j;
     } 
   }
 flag_sortlist[max_record] =1;
 sortlist[k] = max_record;
}

  // sort the trs
 for(let i = 0 ;i < data_count;i++){
     let  cur_tr = tbodyvalue.childNodes[i];
     for(let j = 0;j <each_data_len;j++){
       cur_tr.childNodes[j+2].innerText = datalist[sortlist[data_count-1-i]*each_data_len+j]//row: sortlist[i]  col:j
     }
     cur_tr.childNodes[1].innerText = namelist[sortlist[data_count-1-i]];
   }
   for(let k = 0;k< data_count;k++){
     sort_list_index[k] = sortlist[data_count-1-k];
   }

};
choose = (idx) => {
  
  choose_arrs[choose_perround++] =parseInt(idx);
  if(choose_perround === questionsnum){
      for(let j = 0 ;j< questionsnum;j++)this.runner.choose(choose_arrs[j]);
    
  }
 
};
stopInteraction = () => {
  sort_list_index = [0,1,2];
  this.prevIndices.delete();
  this.runner.delete();
  this.props.showResult();
};
nextinteraction =()=>{

  let  tablevalue =this.refs.items;
  let  tbodyvalue = tablevalue.childNodes[1];//tbodyvalue

  questionsnum = tbodyvalue.childElementCount;
  for(let i = 0;i < questionsnum;i++){
    this.choose(sort_list_index[i]);
  }
  choose_arrs=[0,0,0];
  const convexHullVertices = vector2Array2D(
    window.Module.readConvexHullVertices());
    this.props.updateConvexHull(convexHullVertices);
  const currIndices = this.runner.getCandidatesIndices();
  const prunedIndices = getPrunedIndices(this.prevIndices, currIndices);
  const questioNo = this.props.numLeftPoints.length;

  this.props.prunePoints(prunedIndices, questioNo);
  this.prevIndices.delete();
  this.prevIndices = currIndices;

  choose_perround = 0;
  sort_list_index = [0,1,2];
  const num_noSort = this.runner.numLeftPoints_noSort();
  this.props.setLeftPoints_noSort(num_noSort);
  if (currIndices.size() === 1) {
    questionsnum = 3;
    this.state = { pair: [] };
    this.stopInteraction();
  }else if(currIndices.size() > 2){
     questionsnum = 3;
      const indices = this.runner.nextPair(questionsnum);
      this.setState({
             pair: vector2Array(indices)});
   }else{
     questionsnum = 2;
    const indices = this.runner.nextPair(questionsnum);
    this.setState({
           pair: vector2Array(indices)});

   }  
  
};
  /**-------the section of functions(end)-------- */
  render() {
  
    /**set table  colums */
    const ths = [<th key="Option No." style={{verticalAlign:"middle",textAlign:"center"}}>Player</th>];
    ths.push(<th key="Option No." style={{verticalAlign:"middle",textAlign:"center"}}>Name</th>)
    this.attributes.forEach(attr => {
      ths.push(<th key={attr}   style={{textAlign:"center"}}  
       >
        {attr}
        <span style={{display:"inline-block",verticalAlign:"middle", marginLeft:"2px"}}>
            <span class="glyphicon glyphicon-chevron-up" aria-hidden="true" 
            style={{display:"block",cursor:"pointer"}}
            onClick={(e)=>this.ascSortfunction(e)}
            >
            </span>
            <span class="glyphicon glyphicon-chevron-down" aria-hidden="true"
            style={{display:"block",cursor:"pointer"}}
            onClick={(e)=>this.dscSortfunction(e)}
            ></span> 
        </span>   
      </th>
     );
    });
    ths.push(<th key="chooseButton" />);

    const trs = this.state.pair.map((idx, i) => {
      const tds = [<td key="Option No." >{i + 1}</td>];
      tds.push(<td key="Name=" >{this.props.labels[idx]}</td>);
      this.props.candidates[idx].forEach((x, j) => {
        tds.push(<td key={j}>{x}</td>);
      });
      return (
        <tr key={i} data-toggle="tooltip" title={this.props.labels[idx]}
         draggable={true} onDrag={(e)=>this.dragStart}
         onDrop={(e)=>this.drop(e)}
         onDragOver={(e)=>this.dragOver(e)}
         onDragStart={(e)=>this.dragStart(e,i)}
         >
          {tds}
        </tr>
      );
    }); 
 
    return (
      <div class="col-md-6 column pull-left">
        <h2>Sorting displayed players</h2>    
            <table className="table table-hover text-center" ref="items">
              <thead>
                <tr>{ths}</tr>
              </thead>
              <tbody>{trs}</tbody>
    <tr>
    <td style={{textAlign:"center",cursor:"pointer"}}
    onClick={this.nextinteraction}
    colSpan="2"
    >
      <Button type="primary"> next
          <span class="glyphicon glyphicon-forward" aria-hidden="true"
          style={{textAlign:"center",marginLeft:"2px"}}>
          </span>
      </Button>
      
    </td>
   <td style={{cursor:"pointer",textAlign:"center"}}
   onClick={this.stopInteraction}
   colSpan="2"
   >
     <Button type="primary">stop
        <span class="glyphicon glyphicon-stop" aria-hidden="true"
        style={{textAlign:"center",marginLeft:"2px"}}>
        </span>
      </Button>
     
   </td>
   </tr>
            </table>                  
      </div>

    );

   }
};



const mapStateToProps = ({
  labels,
  candidates,
  mask,
  attributes,
  numLeftPoints,
  numLeftPoints_noSort,
  mode
}) => ({
  labels,
  candidates,
  mask,
  attributes,
  numLeftPoints,
  numLeftPoints_noSort,
  mode
});

const mapDispatchToProps = dispatch => ({
  showResult: () => {
    dispatch(setActiveComponent("Result"));
  },
  setLeftPoints: indices => {
    dispatch(setLeftPoints(indices));
  },
  setLeftPoints_noSort: num_noSort => {
    dispatch(setLeftPoints_noSort(num_noSort));
  },
  prunePoints: (indices, step) => {
    dispatch(prunePoints(indices, step));
  },
  updateConvexHull: vertices => {
    dispatch(updateConvexHull(vertices));
  }
});



export default connect(
  mapStateToProps,
  mapDispatchToProps
)(Question);