import { createStore } from "redux";
import rootReducer from "./reducers";
import React from "react";
import { render } from "react-dom";
import { Provider } from "react-redux";
import App from "./components/App";
import { loadDataset } from "./utils";


window.Module = {
  onRuntimeInitialized: async function() {
    const [points, labels] = await loadDataset(
      "./datasets/nba_d3.txt",
      "./datasets/NBA-NAME.txt"
    );
    const attributes = [
      ["Points", { low: 0, high: 1453, smallerBetter: false }],
      ["Rebounds", { low: 0, high: 350, smallerBetter: false }],
      ["Steals", { low: 0, high: 81, smallerBetter: false }]
    ];
    const mask = {};
    attributes.forEach(([attr, config]) => {
      mask[attr] = 1;
    });

    const store = createStore(
      rootReducer,
      { points, labels, attributes, mask }
      // window.__REDUX_DEVTOOLS_EXTENSION__ && window.__REDUX_DEVTOOLS_EXTENSION__()
    );
    render(
      <Provider store={store}>
        <App />
      </Provider>,
      document.getElementById("root")
    );
  }
};

const script = document.createElement("script");
script.src = "web.js";
script.async = true;
document.body.appendChild(script);
