import React from "react";
import { connect } from "react-redux";



import Highcharts from "../../node_modules/highcharts/highcharts.js";
import highcharts3d from  "../../node_modules/highcharts/highcharts-3d.js";
highcharts3d(Highcharts);


// statistics including the number of cars pruned/left.
class Stats extends React.Component {
  constructor(props) {
    super(props);
    this.attributes = [];
    this.chart = [];
    this.props.attributes.forEach(([attr, config]) => {
      if (this.props.mask[attr]) {
        this.attributes.push(attr);
      }
    });
    this.cur_left_points=[];
    this.MouseDown = this.MouseDown.bind(this);
    
  }
 
componentDidUpdate(){
  let  datalist =[];
    for(let  i = 0; i<this.cur_left_points.length;i++){
       let each_data = this.props.candidates[this.cur_left_points[i]];
       let  threeds_data=[];
         for(let j =0;j < 3;j++)
           {
             threeds_data[j] =  each_data[j];
            }
       datalist.push(threeds_data);
    }
  
   this.chart = new Highcharts.Chart({
    chart: {   
        renderTo:'container',       
        margin:50,
        type:'scatter',
        height:400,
        options3d: {
            enabled: true,
            alpha: 10,
            beta: 15,
            depth: 250,
            viewDistance: 5,
            frame: {
                bottom: { size: 1, color: 'rgba(0,0,0,0.2)' },
                back: { size: 1, color: 'rgba(0,0,0,0.2)' },
                side: { size: 1, color: 'rgba(0,0,0,0.2)' }
            },
          fitToPlot:true
        }
    },
    credits:{
      enabled:false
    },
    title: {
        text: '<b>3D scatter plot</b>'
    },
    subtitle:{
      text:'click and drag the mouse to rotate the drawing area'
    },
    plotOptions: {
        scatter: {
            width: 100,
            height: 100,
            depth: 100
        }
    },
    yAxis: {
      min: 0,
      max: 350,
      title: {
        text:'<b>Rebounds(yAxis)</>'
      },
  },
  xAxis: {
      min: 0,
      max: 1500,
      gridLineWidth: 1,
      title: {
        text:'<b>Points(xAxis)</>'
      },
  },
  zAxis: {
      min: 0,
      max: 350,
      offset:2,
      title: {
        text:'<b>Steals(zAxis)</>'
      },
  },
    legend: {
        enabled: false
    },
    series: [{
        name: ' NBA  data',
        colorByPoint: true,
        data: datalist
    }]
    });

}
MouseDown=(e)=>{
  e = this.chart.pointer.normalize(e);
  let posX = e.pageX;
  let posY = e.pageY;
  let alpha = this.chart.options.chart.options3d.alpha;
  let beta = this.chart.options.chart.options3d.beta;
  let newAlpha = 0;
  let newBeta = 0;
  let sensitivity =6;
  let chart = this.chart;
  document.onmousemove=function(e){
    newBeta = beta  + (posX - e.pageX) / sensitivity;

    newBeta = Math.min(100, Math.max(-100, newBeta));
    chart.options.chart.options3d.beta = newBeta;
    newAlpha = alpha + (e.pageY - posY) / sensitivity;

    newAlpha = Math.min(100, Math.max(-100, newAlpha));
    chart.options.chart.options3d.alpha = newAlpha;
    chart.redraw(false); 
    e.target.onmouseup=function(e){
      document.onmousemove = null;
      e.target.onmouseup = null;
    }
   
  }

}
  render() {
    
    //--------------
    this.cur_left_points = this.props.leftPoints;
    return (
      <div class="col-md-6 column pull-left" >
            <div  id="container"
            onMouseDown={(e)=>this.MouseDown(e)}
            ></div>
      </div>
        );  
  }
  //--------------------------


}

const mapStateToPropsStats = ({
  candidates,
  leftPoints,
  attributes,
  mask
}) => ({
  candidates,
  leftPoints,
  attributes,
  mask
});


export default connect(mapStateToPropsStats)(Stats);


